<template>
  <div id="signup-form" class="d-flex flex-column" style="flex: 1">
    <form @submit.prevent="signupSubmit()">
      <v-container >
        <v-row align="center" justify="center">
          <v-col cols="2">
            <label class="primary--text" for="ID"></label>
          </v-col>
          <v-col cols="4">
            <input id="ID" type="text" @blur="idCheck(credentials)" v-model="credentials.id" required
            maxlength="12"
            minlength="4"/>
          </v-col>
          <v-col cols="5">
            <span class="check secondary--text" v-if="!idValidate">4~12    </span>
            <span class="check error--text" v-else-if="idValidate && !idFlag">   .</span>
            <span class="check secondary--text" v-else-if="idValidate && idFlag">   .</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="2">
            <label class="primary--text" for="PW">PW</label>
          </v-col>
          <v-col cols="4">
            <input id="PW" type="password" 
            v-model="credentials.password" 
            required
            minlength="8"
            maxlength="16"/>
          </v-col>
          <v-col cols="5">
            <span class="check secondary--text" v-if="!PWValidFlag">8~16    </span>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="2">
            <label class="primary--text" for="checkPW">PW </label>
          </v-col>
          <v-col cols="4">
            <input id="checkPW" type="password" 
            v-model="credentials.password2" 
            required
            :rules="passwordRule"
            @blur="passwordCheckValid"/>
          </v-col>
          <v-col cols="5">
            <span class="check error--text"
              v-if="!passwordCheckFlag">  </span>
            <span class="check secondary--text" v-if="passwordCheckFlag"> .</span>
          </v-col>
        </v-row>
        <v-row align="center" justify="center">
          <v-col cols="2">
            <label class="primary--text" for="nickname"></label>
          </v-col>
          <v-col cols="4">
            <input id="nickname" @blur="nicknameCheck(credentials)" v-model="credentials.nick"
            type="text"
            required
            maxlength="6"/>
          </v-col>
          <v-col cols="5">
            <span class="check secondary--text" v-if="!nickValidate">6   </span>
            <span class="check error--text" v-else-if="nickValidate&&!nicknameFlag">   .</span>
            <span class="check secondary--text" v-else-if="nickValidate&&nicknameFlag">   .</span>
          </v-col>
        </v-row>
      </v-container>
      <div class="d-flex justify-center align-center">
        <v-btn color="primary" elevation="0" class="submit black--text" type="submit"></v-btn>
      </div>    
    </form>
    <div class="d-flex justify-center align-center">
      <router-link to="/login">
        <v-btn color="primary" elevation="0" class="submit black--text" type="submit">  </v-btn>
      </router-link>
    </div>  
  </div>
</template>

<script>
import {mapActions, mapGetters} from 'vuex'

export default {
  name: "SignupForm",
  components: {
  },
  data() {
    return {
      credentials: {
        id: '',
        nick: '',
        password: '',
        password2: '',
      },
      passwordCheckFlag : false,
      pwShow: false,
      idCheckFlag: false,
      nicknameCheckFlag: false,
      PWValidFlag: false,
    }
  },
  computed: {
    ...mapGetters(['authError', 'passwordFlag', 'idFlag', 'nicknameFlag']),
    idValidate(){
      return /^[A-Za-z0-9]+$/.test(this.credentials.id)
    },
    nickValidate(){
      return /^[A-Za-z0-9-]+$/.test(this.credentials.id)
    },
  },
  methods: {
    ...mapActions(['passwordCheck', 'signup', 'nicknameCheck', 'idCheck']),
    passwordCheckValid() {
      if (this.credentials.password === this.credentials.password2) {
        this.passwordCheckFlag = true
      } else {
        this.passwordCheckFlag = false
      }
    },
    PWValidate(){
      if (/^(?=.*[a-z])(?=.*[A-Z])(?=.*[0-9]).{8,16}$/.test(this.credentials.password)) {
        this.PWValidFlag = true
      } else {
        this.PWValidFlag = false
      }
    },
    signupSubmit(){
      if (this.passwordCheckFlag === true && this.idFlag === true && this.nicknameFlag === true) {
        this.passwordCheck(this.credentials)
      } else {
        alert(' ')
      }
    },
  }
  };
</script>

<style scoped>
.check {
  font-weight: bold;
}
.submit {
  width: 25rem;
  height: 2rem;
  font-size: 1rem;
  font-weight: bold;
  border-radius: 5px;
  margin-top: 5%;
}
#overlap{
  font-size: .9rem;
  font-weight: 800;
  line-height: 1.6;
  margin-left: 5%;
}
label{
  font-weight: bold;
  font-size: 1.2rem;
}
input {
  background: white;
  border-radius: 5px;
  border: 3px solid #ffb82f;
  font-size: 12px;
}
</style>
